.pat-collapsible:not(.context-menu):not(.pat-modal-pop-over) {
	position: relative;
	// min-height: 2em;

	&:not(.pat-modal-pop-over) {
		// margin-bottom: 0.5em;

		 > *:first-child:before {
			content: "â–¼";
			text-align: center;
			width: 1.5em;
			display: inline-block;

			-moz-transition: all 0.5s;
			-webkit-transition: all 0.5s;
			transition: all 0.5s;
		}

		&.closed > *:first-child:before {
			-moz-transform: rotate(-90deg);
			-webkit-transform: rotate(-90deg);
			transform: rotate(-90deg);
		}

		 > .panel-content {
			padding-top: 1em;
			padding-bottom: 1em;
		}
		
		> *:first-child {
			// @include pat-button();

			font-weight: normal;
			width: 100%;

			-moz-box-sizing: border-box;
			-webkit-box-sizing: border-box;
			box-sizing: border-box;
			// font-size: 0.8em;
		}
	}

	> .collapsible-closed,
	> .collapsible-open {
		margin: 0;
		cursor: pointer;
		color: $colour-accent;
		.warning & { 
			color: white ;
		}

		font-weight: 300;

		&:before {
			cursor: default;
			font-size: 0.8em;
			float: left;
			color: rgba(0,0,0,0.5);
			margin-right: 0.3em;
		}

		&:hover {
			text-decoration: underline;
		}
	}

	.collapsible-closed {
		.warning & { 
			text-decoration: underline;
		}
	}

	.panel-content {
		-moz-transition: padding 0.5s;
		-webkit-transition: padding 0.5s;
		transition: padding 0.5s;
	}

	&.show-more {
		padding-bottom: 2em;
	}
	
	&.closed,
	&.open.in-progress  {

		.show-more-or-less {
			position: static;
		}

		.panel-content {
			padding: 0 0 1em;
		}
	}

	&.open,
	&.closed.in-progress {

		.panel-content {
			padding-top: 1em;
			padding-bottom: 1em;
		}

		.show-more-or-less {
			position: absolute;
			bottom: 2em;
			left: 0;
			right: 0;
			margin: 0;
		}
	}

	&.open.show-more,
	&.open.show-more.in-progress {
		padding-bottom: 3em;

		.panel-content {
			padding: 0;
		}
	}
}